import React from 'react'

export default function PdctCompo(props) {
  let a = [];
  if (props.details != null) {
    Object.values(props.details).map((ele)=>{
    a.push(<p className="col-11  m-2 text-capitalize text-start fw-bolder" style={{ backgroundColor: `${props.color}`}}>{ele}</p>);
  });
}
  return (
    <>
      <div className="row">
        <div className={`col-12 border border-0  border-top-${props.border} border-dark`}>
          <span className="fs-3 fw-bolder">{props.name}</span>
          <div className="row p-2">
            <p className="col-11">{props.defination}</p>
            {a.map((ele) => {
              return ele;
            })}
          </div>
        </div>
      </div>
    </>
  )
}
