import React from 'react'

export default function Story(props) {
  let a = [];
    if (props.details != null) {
        Object.values(props.details).map((ele) => {
            a.push(<p className={`col-${props.col} m-${props.offset} text-capitalize text-start fw-bolder`} style={{ backgroundColor: `${props.color}` }}>{ele}</p>);
        })
    }
  return (
    <>
      <div className="row">
        <div className={`col-12 ms-3`}>
          <span className={`text-${props.text}`}><h2 className='fw-bolder fs-2'>{props.name}</h2></span>
          <span className={`text-${props.text}`}><h2 className='fs-3'>{props.happy}</h2></span>
          <div className="row">
            {a.map((ele) => {
              return ele;
            })}
          </div>
        </div>
      </div>
    </>
  )
}
