import React, { useState } from 'react'
import './Style.css'
import PdctCompo from './PdctCompo'
export default function LNM() {
  const [render, setrender] = useState({ Tools: [[], [], []], Appli: [[], [], []], Software: [[], [], []], Compo: [[], [], []],pro:[] });
  return (
    <>  <div className="mainlnm overflow-scroll p-3">
        <div className="row ">
          <div className="container-lnm ">
            <div className="box-lnm" id="box-lnm1"><h5>Tools/Method/Theories/Application/process/involved</h5> <h5 Style={"margin-right: 3px;"}>During</h5></div>
            <div className="box-lnm" id="box-lnm2"><PdctCompo col={10} color={"#8BFD00"} details={render.Tools[2]} /></div>
            <div className="box-lnm" id="box-lnm3"><h5>BE IV </h5><h5>Applicable standard and design specifications/ principle Experiment</h5></div>
            <div className="box-lnm" id="box-lnm4"><PdctCompo col={10} color={"#8BFD00"} details={render.Appli[2]} /></div>
            <div className="box-lnm" id="box-lnm5"><h5 Style={"margin-right: 3px;"}>During</h5></div>
            <div className="box-lnm" id="box-lnm6"><PdctCompo col={10} color={"#8BFD00"} details={render.Tools[1]} /></div>
            <div className="box-lnm" id="box-lnm7"><h5 Style={"margin-left: 3px;"}>BE III</h5></div>
            <div className="box-lnm" id="box-lnm8"><PdctCompo col={10} color={"#8BFD00"} details={render.Appli[1]} /></div>
            <div className="box-lnm" id="box-lnm9"><h5 Style={"margin-right: 3px;"}> During</h5></div>
            <div className="box-lnm" id="box-lnm10"><PdctCompo col={8} color={"#8BFD00"} details={render.Tools[0]} /></div>
            <div className="box-lnm" id="box-lnm11"><h5 Style={"margin-left: 3px;"}>BE II</h5></div>
            <div className="box-lnm" id="box-lnm12"><PdctCompo col={10} color={"#8BFD00"} details={render.Appli[0]} /></div>
            <div className="box-lnm" id="box-lnm13"><PdctCompo col={12} color={"#8BFD00"} details={render.pro} name={"Purpose / Product Consept"} /></div>
            <div className="box-lnm" id="box-lnm14"><PdctCompo col={10} color={"#8BFD00"} details={render.Software[2]} /></div>
            <div className="box-lnm" id="box-lnm15"> <h5> Software / Simutation / Mathematical Requirment</h5> </div>
            <div className="box-lnm" id="box-lnm16"><PdctCompo col={10} color={"#8BFD00"} details={render.Software[1]} /></div>
            <div className="box-lnm" id="box-lnm17"></div>
            <div className="box-lnm" id="box-lnm18"><PdctCompo col={10} color={"#8BFD00"} details={render.Software[0]} /></div>
            <div className="box-lnm" id="box-lnm19"></div>
            <div className="box-lnm" id="box-lnm21"><PdctCompo col={10} color={"#8BFD00"} details={render.Compo[1]} /></div>
            <div className="box-lnm" id="box-lnm22"></div>
            <div className="box-lnm" id="box-lnm23"><PdctCompo col={10} color={"#8BFD00"} details={render.Compo[0]} /></div>
            <div className="box-lnm" id="box-lnm24"></div>
            <div className="box-lnm" id="box-lnm25"><PdctCompo col={10} color={"#8BFD00"} details={render.Compo[2]} /></div>
            <div className="box-lnm" id="box-lnm26"><h5>Component Material strength (exploaration varieties/testing requirment)</h5></div>
          </div>
        </div>
        </div>
        <div className="row mt-5 ">
          <div className="d-flex flex-row flex-wrap">
            <h5>Tools /Method Thei...</h5>
            <select className="form-select " id="sem" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="4">BE IV</option>
              <option value="3">BE III</option>
              <option value="2">BE II</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="Tools" placeholder="Enter the data" />
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem").value !== '' && document.getElementById("Tools").value !== '') {
                let index = document.getElementById("sem").value;
                console.log(parseInt(index) - 2)
                render.Tools[parseInt(index) - 2].push(document.getElementById("Tools").value);
                document.getElementById("Tools").value = '';
                setrender({ ...render });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem").value !== '') {
                let index = document.getElementById("sem").value;
                console.log(parseInt(index) - 2)
                render.Tools[parseInt(index) - 2].pop();
                setrender({ ...render });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex flex-row flex-wrap mt-5">
            <h5>Applicable standard..</h5>
            <select className="form-select " id="sem1" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for Applicable standard..</option>
              <option value="4">BE IV</option>
              <option value="3">BE III</option>
              <option value="2">BE II</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="appli" placeholder="Enter the data" /><button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem1").value !== '' && document.getElementById("appli").value !== '') {
                let index = document.getElementById("sem1").value;
                console.log(parseInt(index) - 2)
                render.Appli[parseInt(index) - 2].push(document.getElementById("appli").value);
                document.getElementById("appli").value = '';
                setrender({ ...render });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem1").value !== '') {
                let index = document.getElementById("sem1").value;
                console.log(parseInt(index) - 2)
                render.Appli[parseInt(index) - 2].pop();
                setrender({ ...render });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex flex-row flex-wrap mt-5">
            <h5>Software simultation.</h5>
            <select className="form-select " id="sem2" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for Software simultation.</option>
              <option value="4">BE IV</option>
              <option value="3">BE III</option>
              <option value="2">BE II</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="soft" placeholder="Enter the data" /><button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem2").value !== '' && document.getElementById("soft").value !== '') {
                let index = document.getElementById("sem2").value;
                console.log(parseInt(index) - 2)
                render.Software[parseInt(index) - 2].push(document.getElementById("soft").value);
                document.getElementById("soft").value = '';
                setrender({ ...render });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem2").value !== '') {
                let index = document.getElementById("sem2").value;
                console.log(parseInt(index) - 2)
                render.Software[parseInt(index) - 2].pop();
                setrender({ ...render });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex flex-row flex-wrap mt-5">
            <h5>Component material..</h5>
            <select className="form-select " id="sem3" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem Component material..</option>
              <option value="4">BE IV</option>
              <option value="3">BE III</option>
              <option value="2">BE II</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="compo" placeholder="Enter the data" /><button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem3").value !== '' && document.getElementById("compo").value !== '') {
                let index = document.getElementById("sem3").value;
                console.log(parseInt(index) - 2)
                render.Compo[parseInt(index) - 2].push(document.getElementById("compo").value);
                document.getElementById("compo").value = '';
                setrender({ ...render });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("sem3").value !== '') {
                let index = document.getElementById("sem3").value;
                console.log(parseInt(index) - 2)
                render.Compo[parseInt(index) - 2].pop();
                setrender({ ...render });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex flex-row flex-wrap mt-5">
            <h5>Product / purpose....</h5>
            <input type="text" style={{ width: 500 }} className="form-control" id="pro" placeholder="Enter the data" /><button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("pro").value !== '') {
                render.pro.push(document.getElementById("pro").value);
                document.getElementById("pro").value = '';
                setrender({ ...render });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
                
                render.pro.pop();
                setrender({ ...render });
              }
            }>Remove</button>
          </div>
        </div>
    </>
  )
}
