import React, { useState } from 'react'
// import Details from './Details'
import Enviroment from './Enviroment';
import Activity from './Activity';

export default function AEIOU() {
  const [style, setstyle] = useState({ intro: {}, color: ["#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00"], details: [[], [], [], [], []], detai: [], detailIn: [], detailUser: [], detailActivity: [], detailObject: [] });
  return (
    <>
    <div className="main-aeiou overflow-scroll">
            <div className="container-AEIOU-Intro d-print-grid" style={{ minWidth: 1050, maxWidth: 1050 }}>
              <div className="item-AEIOU-Intro text-start fs-2 fw-bolder" id="item-AEIOU-Intro-1">AEIOU Summary.</div>
              <div className="item-AEIOU-Intro fs-6 text-start" id="item-AEIOU-Intro-2">
                Group Id: <p className='fw-bolder fs-6 d-inline-block'>{style.intro.id}</p><br />
                Domain Name:<p className='fw-bolder fs-6 d-inline-block'>{style.intro.DomainName}</p>
              </div>
              <div className="item-AEIOU-Intro" id="item-AEIOU-Intro-3">Date:{style.intro.date}</div>
              <div className="item-AEIOU-Intro" id="item-AEIOU-Intro-4">Version:{style.intro.version}</div>
            </div>
            <div className="contain-AEIOU d-print-grid ">
              <div className="item-AEIOU" id="item-1-AEIOU"><Enviroment name="Enviroment" color={style.color[0]} detai={style.details[0]} /></div>
              <div className="item-AEIOU" id="item-2-AEIOU"><Enviroment name="interaction" color={style.color[1]} detai={style.details[1]} /></div>
              <div className="item-AEIOU" id="item-3-AEIOU"><Enviroment name="Objects" color={style.color[2]} detai={style.details[2]} /></div>
              <div className="item-AEIOU" id="item-5-AEIOU"><Activity name="Activities" color={style.color[3]} mar={3} detai={style.details[3]} /></div>
              <div className="item-AEIOU" id="item-6-AEIOU"><Activity name="user" color={style.color[4]} mar={3} detai={style.details[4]} /></div>
            </div>
          </div>
        <div className="row mt-5 d-print-none">
          <div className="d-flex flex-row flex-wrap" >
            <h6>Fill the introuduction:&nbsp;</h6>
            <input type="text" style={{ width: 250 }} className="form-control" id="group" placeholder="Enter the data" />
            <input type="text" style={{ width: 250 }} className="form-control" id="domain" placeholder="Enter the data" />
            <input type="text" style={{ width: 250 }} className="form-control" id="date" placeholder="Enter the data" />
            <input type="text" style={{ width: 250 }} className="form-control" id="version" placeholder="Enter the data" />
            <button className="btn btn-outline-secondary" type="button" onClick={() => {
              if (document.getElementById("group").value !== '' && document.getElementById("domain").value !== '' && document.getElementById("date").value !== '' && document.getElementById("version").value !== '') {
                style.intro.id = document.getElementById("group").value;
                style.intro.DomainName = document.getElementById("domain").value;
                style.intro.date = document.getElementById("date").value;
                style.intro.version = document.getElementById("version").value;
                setstyle({ ...style });
              }
              else {
                alert("Please enter all the details in one go ");
              }
            }}>Button</button>
          </div>
          <div className="d-flex flex-row flex-wrap mt-5">
            <h6>Select the Component:&nbsp; </h6>
            <select className="form-select " id="compo" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="0">Enviroment</option>
              <option value="1">Interaction </option>
              <option value="2">object</option>
              <option value="3">Activity</option>
              <option value="4">user</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="Tools" placeholder="Enter the data" />
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("compo").value !== '' && document.getElementById("Tools").value !== '') {
                let index = document.getElementById("compo").value;
                style.details[parseInt(index)].push(document.getElementById("Tools").value);
                document.getElementById("Tools").value = '';
                setstyle({ ...style });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("compo").value !== '') {
                let index = document.getElementById("compo").value;
                style.details[parseInt(index)].pop();
                setstyle({ ...style });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex mt-5 flex-row flex-wrap">
            <h6>Select color for compo:&nbsp;</h6>
            <select className="form-select " id="compo1" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="0">Enviroment</option>
              <option value="1">Interaction </option>
              <option value="2">object</option>
              <option value="3">Activity</option>
              <option value="4">user</option>
            </select>
            <select class="form-select" id="colorenvi" style={{ width: 350 }} aria-label="Example select with button addon" onChange={() => {
              if (document.getElementById("compo1").value !== '') {
                let index = document.getElementById("compo1").value;
                style.color[parseInt(index)] = document.getElementById("colorenvi").value;
                setstyle({ ...style });
              }
            }}>
              <option selected value="#8BFD00">Green</option>
              <option value="#FFBB0E">orange</option>
              <option value="#eb10bf">pink</option>
              <option value="#eb1018">red</option>
            </select>
          </div>
        </div>
    </>
  )
}
