import React, { useState } from 'react'
import PdctCompo from './PdctCompo'
import './Style.css'
export default function ProductDevelo() {
  const [style, setstyle] = useState({ num: 0, intro: {}, color: ["#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00"], details: [[], [], [], [], [], [], [], []] });
  return (
    <>
            <div className="mainprde overflow-scroll p-3">
            <div class="container-produc-intro">
              <div class="item-produc text-start fw-bolder fs-3">Product Development Canvas</div>
              <div class="item-produc text-start fs-5">Team/Date/Version: <span className='fw-bolder'>{style.intro.team}/{style.intro.date}/{style.intro.version}</span></div>
            </div>
            <div className="contain ">
              <div className="item" id="item1"><PdctCompo border={0} name="Purpose" col={11} color={style.color[0]} details={style.details[0]} defination={"What is the pupose of this concept you're Developing does it solve a problem, or it enhances a certain experincence"} /> </div>
              <div className="item" id="item2"><PdctCompo border={0} name="Product Experience" col={5} color={style.color[1]} details={style.details[1]} /></div>
              <div className="item" id="item3"><PdctCompo border={0} name="Customer Revalidation" col={11} color={style.color[2]} details={style.details[2]} /></div>
              <div className="item" id="item4"><PdctCompo border={0} name="Product Functions" col={5} color={style.color[3]} details={style.details[3]} /></div>
              <div className="item" id="item5"><PdctCompo border={0} name="People" col={11} color={style.color[4]} details={style.details[4]} /></div>
              <div className="item" id="item6"><PdctCompo border={0} name="Product Features" col={5} color={style.color[5]} details={style.details[5]} /></div>
              <div className="item" id="item7"><PdctCompo border={0} name="Reject, Redsign, Retain" col={11} color={style.color[6]} details={style.details[6]} /></div>
              <div className="item" id="item8"><PdctCompo border={0} name="Components" col={5} color={style.color[7]} details={style.details[7]} /></div>
            </div>
            </div>
         
      
        <div className="row mt-5 ">
          <div className="d-flex flex-row flex-wrap">
            <h6>Select the Component:&nbsp; </h6>
            <select className="form-select " id="compo" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="0">Purpose</option>
              <option value="1">Product Experience</option>
              <option value="2">Customer Revalidation</option>
              <option value="3">Product Functions</option>
              <option value="4">People</option>
              <option value="5">Product Features</option>
              <option value="6">Reject, Redsign, Retain</option>
              <option value="7">Components</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="Tools" placeholder="Enter the data" />
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("compo").value !== '' && document.getElementById("Tools").value !== '') {
                let index = document.getElementById("compo").value;
                style.details[parseInt(index)].push(document.getElementById("Tools").value);
                document.getElementById("Tools").value = '';
                setstyle({ ...style });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("compo").value !== '') {
                let index = document.getElementById("compo").value;
                style.details[parseInt(index)].pop();
                setstyle({ ...style });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex mt-5 flex-row flex-wrap">
            <h6>Select color for compo:&nbsp;</h6>
            <select className="form-select " id="compo1" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="0">Purpose</option>
              <option value="1">Product Experience</option>
              <option value="2">Customer Revalidation</option>
              <option value="3">Product Functions</option>
              <option value="4">People</option>
              <option value="5">Product Features</option>
              <option value="6">Reject, Redsign, Retain</option>
              <option value="7">Components</option>
            </select>
            <select class="form-select" id="colorenvi" style={{ width: 350 }} aria-label="Example select with button addon" onChange={() => {
              if (document.getElementById("compo1").value !== '') {
                let index = document.getElementById("compo1").value;
                style.color[parseInt(index)] = document.getElementById("colorenvi").value;
                setstyle({ ...style });
              }
            }}>
              <option selected value="#8BFD00">Green</option>
              <option value="#FFBB0E">orange</option>
              <option value="#eb10bf">pink</option>
              <option value="#eb1018">red</option>
            </select>

          </div>
        </div>

     
    </>
  )
}
