import React, { useState } from 'react'
import Activity from './Activity'
import Story from './Story'

export default function Empathy() {
  const [style, setstyle] = useState({ num: 0, intro: {}, color: ["#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00"], details: [[], [], [], [], [], []], detailsUser: [], detailsStackeHol: [], detailsHappy1: [], detailsad1: [], detailsHappy2: [], detailsad2: [] });

  return (
    <>  <div className="mainempathy overflow-scroll">
        <div className="row ms-2 border border-2 border-dark" style={{ maxWidth: 1200, minWidth: 1200 }}>
          <div class="container-Empathy-intro ms-2 mb-1">
            <div class="item-empathy-intro text-start fs-3" id="item-empathy-intro-1">Design For :<span className='fw-bolder'> {style.intro.id}</span></div>
            <div class="item-empathy-intro text-start fs-3" id="item-empathy-intro-2">Design by :<span className='fw-bolder'> {style.intro.by}</span></div>
            <div class="item-empathy-intro text-start fs-3" id="item-empathy-intro-3">Date :<span className='fw-bolder'> {style.intro.date}</span></div>
            <div class="item-empathy-intro text-start fs-3" id="item-empathy-intro-4">Version :<span className='fw-bolder'>  {style.intro.ver}</span></div>
          </div>
          <div className="container-Empathy pb-2 ms-2">
            <div className="item-Empathy " id="item-Empathy-1"><Activity name="USER" mar={2} color={style.color[0]} detai={style.details[0]} /></div>
            <div className="item-Empathy" id="item-Empathy-2"><Activity name="STACKHOLDER" mar={2} color={style.color[1]} detai={style.details[1]} /></div>
            <div className="item-Empathy" id="item-Empathy-3"><Story name="STORY BORDING:" happy="HAPPY" text="start" color={style.color[2]} col={11} offset={0} details={style.details[2]} /></div>
            <div className="item-Empathy" id="item-Empathy-4"><Story bottom={0} happy="SAD" text="start" color={style.color[3]} col={11} offset={0} details={style.details[3]} /></div>
            <div className="item-Empathy" id="item-Empathy-5"><Story bottom={0} happy="HAPPY" text="start" color={style.color[4]} col={11} offset={0} details={style.details[4]} /></div>
            <div className="item-Empathy" id="item-Empathy-6"><Story bottom={2} happy="SAD" text="start" color={style.color[5]} col={11} offset={0} details={style.details[5]} /></div>
          </div>
        </div>
        </div>
        <div className="row mt-5">

          <div className="d-flex flex-wrap mt-5 flex-row">
            <h6>Fill the introuduction:&nbsp;</h6>
            <input type="text" style={{ width: 250 }} className="form-control" id="group" placeholder="Enter the Name" />
            <input type="text" style={{ width: 250 }} className="form-control" id="domain" placeholder="Enter the team" />
            <input type="text" style={{ width: 250 }} className="form-control" id="date" placeholder="Enter the date" />
            <input type="text" style={{ width: 250 }} className="form-control" id="version" placeholder="Enter the version" />
            <button className="btn btn-outline-secondary" type="button" onClick={() => {
              if (document.getElementById("group").value !== '' && document.getElementById("domain").value !== '' && document.getElementById("date").value !== '' && document.getElementById("version").value !== '') {
                style.intro.id = document.getElementById("group").value;
                style.intro.by = document.getElementById("domain").value;
                style.intro.date = document.getElementById("date").value;
                style.intro.ver = document.getElementById("version").value;
                setstyle({ ...style });
              }
              else {
                alert("Please enter all the details in one go ");
              }
            }}>Button</button>
          </div>
          <div className="d-flex flex-wrap mt-5 flex-row">
            <h6>Select the Component:&nbsp; </h6>
            <select className="form-select " id="compo" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="0">User</option>
              <option value="1">StackHolder </option>
              <option value="2">Happy</option>
              <option value="3">sad</option>
              <option value="4">happy</option>
              <option value="5">sad</option>
            </select>
            <input type="text" style={{ width: 500 }} className="form-control" id="Tools" placeholder="Enter the data" />
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("compo").value !== '' && document.getElementById("Tools").value !== '') {
                let index = document.getElementById("compo").value;
                style.details[parseInt(index)].push(document.getElementById("Tools").value);
                document.getElementById("Tools").value = '';
                setstyle({ ...style });
              }
            }} >ADD</button>
            <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
              if (document.getElementById("compo").value !== '') {
                let index = document.getElementById("compo").value;
                style.details[parseInt(index)].pop();
                setstyle({ ...style });
              }
            }}>Remove</button>
          </div>
          <div className="d-flex mt-5 flex-row flex-wrap">
            <h6>Select color for compo:&nbsp;</h6>
            <select className="form-select " id="compo1" style={{ width: 350 }} aria-label=".form-select-lg example">
              <option selected value="">Select sem for tools and method</option>
              <option value="0">User</option>
              <option value="1">StackHolder </option>
              <option value="2">Happy</option>
              <option value="3">sad</option>
              <option value="4">happy</option>
              <option value="5">sad</option>
            </select>
            <select class="form-select" id="colorenvi" style={{ width: 350 }} aria-label="Example select with button addon" onChange={() => {
              if (document.getElementById("compo1").value !== '') {
                let index = document.getElementById("compo1").value;
                style.color[parseInt(index)] = document.getElementById("colorenvi").value;
                setstyle({ ...style });
              }
            }}>
              <option selected value="#8BFD00">Green</option>
              <option value="#FFBB0E">orange</option>
              <option value="#eb10bf">pink</option>
              <option value="#eb1018">red</option>
            </select>
          </div>
        </div>
      

    </>
  )
}
