import React from 'react'

export default function Enviroment(props) {
    let a = [];
    if (props.detai != null) {
        Object.values(props.detai).map((ele) => {
            a.push(<p className="col-10  m-2 text-capitalize text-start fw-bolder" style={{ backgroundColor: `${props.color}` }}>{ele}</p>);
        })
    }
    return (
        <>
            <div className="row">
                <div className={`col-12 ms-3`} style={{ minHeight: 250 }}>
                    <span className='text-start'><h2>{props.name}:</h2></span>
                    <div className="row">
                        {a.map((ele) => {
                            return ele;
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}
