import React, { useState } from 'react'
import Story from './Story'
import Activity from './Activity'

export default function Ideation() {
  const [style, setstyle] = useState({ intro: {}, color: ["#8BFD00", "#8BFD00", "#8BFD00", "#8BFD00"], details: [[], [], [], []], detailsPeople: [], detailsActicity: [], detailsSituation: [], detailsContext: [] });
  return (
    <>
      <div className="mainideation overflow-scroll p-3 ">
        <div className="row m-3 border border-2 border-dark " style={{ maxWidth: 1100, minWidth: 1100 }}  >
          <div class="container-ideation-intro ms-3">
            <div class="item-ideation fs-4 text-start">The Ideanaut : <span className='fw-bolder'>Ideation Canvas</span></div>
            <div class="item-ideation fs-4 text-start">Project: <span className='fw-bolder'>{style.intro.project}</span></div>
            <div class="item-ideation fs-4  text-start">Team: <span className='fw-bolder'>{style.intro.team}</span> </div>
          </div>
          <div class="container-Ideation ms-3 mb-2">
            <div class="item-Ideation" id="item-Ideation-1"><Story happy="people" color={style.color[0]} col={3} offset={3} details={style.details[0]} /></div>
            <div class="item-Ideation" id="item-Ideation-2"><Activity name="Activities" color={style.color[1]} mar={2} detai={style.details[1]} /></div>
            <div class="item-Ideation" id="item-Ideation-3"><Activity name="situation/Context/Location" mar={2} color={style.color[2]} detai={style.details[2]} /></div>
            <div class="item-Ideation" id="item-Ideation-4"><Story happy="Props\Possible solution" color={style.color[3]} col={5} offset={2} details={style.details[3]} /></div>
          </div>
        </div>
      </div>
      <div className="row mt-5">
        <div className="d-flex flex-wrap flex-row">
          <h6>Fill the introuduction:&nbsp;</h6>
          <input type="text" style={{ width: 250 }} className="form-control" id="project" placeholder="Enter project" />
          <input type="text" style={{ width: 250 }} className="form-control" id="date" placeholder="Enter the team" />
          <button className="btn btn-outline-secondary" type="button" onClick={() => {
            if (document.getElementById("project").value !== '' && document.getElementById("date").value !== '') {
              style.intro.project = document.getElementById("project").value;
              style.intro.team = document.getElementById("date").value;
              setstyle({ ...style });
            }
            else {
              alert("Please enter all the details in one go ");
            }
          }}>Button</button>
        </div>
        <div className="d-flex flex-wrap mt-5 flex-row">
          <h6>Select the Component:&nbsp; </h6>
          <select className="form-select " id="compo" style={{ width: 350 }} aria-label=".form-select-lg example">
            <option selected value="">Select sem for tools and method</option>
            <option value="0">people</option>
            <option value="1">activity </option>
            <option value="2">context</option>
            <option value="3">prope</option>
          </select>
          <input type="text" style={{ width: 500 }} className="form-control" id="Tools" placeholder="Enter the data" />
          <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
            if (document.getElementById("compo").value !== '' && document.getElementById("Tools").value !== '') {
              let index = document.getElementById("compo").value;
              style.details[parseInt(index)].push(document.getElementById("Tools").value);
              document.getElementById("Tools").value = '';
              setstyle({ ...style });
            }
          }} >ADD</button>
          <button type="button" className="btn btn-light border border-2 border-dark" onClick={() => {
            if (document.getElementById("compo").value !== '') {
              let index = document.getElementById("compo").value;
              style.details[parseInt(index)].pop();
              setstyle({ ...style });
            }
          }}>Remove</button>
        </div>
        <div className="d-flex mt-5 flex-row flex-wrap">
          <h6>Select color for compo:&nbsp;</h6>
          <select className="form-select " id="compo1" style={{ width: 350 }} aria-label=".form-select-lg example">
            <option selected value="">Select sem for tools and method</option>
            <option value="0">people</option>
            <option value="1">activity </option>
            <option value="2">context</option>
            <option value="3">prope</option>
          </select>
          <select class="form-select" id="colorenvi" style={{ width: 350 }} aria-label="Example select with button addon" onChange={() => {
            if (document.getElementById("compo1").value !== '') {
              let index = document.getElementById("compo1").value;
              style.color[parseInt(index)] = document.getElementById("colorenvi").value;
              setstyle({ ...style });
            }
          }}>
            <option selected value="#8BFD00">Green</option>
            <option value="#FFBB0E">orange</option>
            <option value="#eb10bf">pink</option>
            <option value="#eb1018">red</option>
          </select>
        </div>
      </div>

    </>
  )
}
